import React, { useEffect, useState } from "react"
import { useTranslation } from "react-i18next"
import { Button, Input, InputGroup, InputGroupText } from "reactstrap"
import MyIcon from "src/core/components/MyIcon/MyIcon"
import { HApi, HUI, HUtils } from "src/core/helpers"
import Setting2FAHelpers, {
  updateSettingOneMethodByResponse,
  getCurrentMethodId,
  getCurrentFA2MethodName,
  getCurrentOTPDescription,
} from "../helpers"
function Generation2FA(props) {
  const { t } = useTranslation()
  const [code, setCode] = useState("")
  const [loadingVerfiy, setLoadingVerfiy] = useState(false)
  const [loadingCheck, setLoadingCheck] = useState(false)

  const onRequestDisableCheck = () => {
    setLoadingCheck(true)
    HApi.apiPost({
      path: "Aauth2FA",
      name: "DisableRequest",
      data: {
        UniqueId: Setting2FAHelpers.UniqueId,
        FA2MethodId: getCurrentMethodId(),
      },
    })
      .then(response => {
        setLoadingCheck(false)
      })
      .catch(error => {
        setLoadingCheck(false)
      })
  }

  const onRequestDisable = () => {
    setLoadingVerfiy(true)
    HApi.apiPost({
      path: "Aauth2FA",
      name: "DisableSubmit",
      data: {
        UniqueId: Setting2FAHelpers.UniqueId,
        InputCode: code,
        FA2MethodId: getCurrentMethodId(),
      },
    })
      .then(response => {
        setLoadingVerfiy(false)
        updateSettingOneMethodByResponse(response)
        HUI.hideInMyModalFromProps(props)
      })
      .catch(error => {
        setLoadingVerfiy(false)
      })
  }

  useEffect(() => {
    onRequestDisableCheck()
  }, [])

  return (
    <div>
      {loadingCheck === true ? (
        <div>Loading...</div>
      ) : (
        <div className="d-flex flex-column">
          <div className="mb-2">{getCurrentOTPDescription()}</div>
          <InputGroup style={{ marginBottom: "1rem" }}>
            <InputGroupText>
              <MyIcon data="type:HiOutlineLockClosed" />
            </InputGroupText>
            <Input
              placeholder={t("setting2fa:generation.placeHolderVerify")}
              value={code}
              onChange={ev => {
                setCode(ev.currentTarget.value)
              }}
              onKeyDown={ev => {
                HUtils.onKeyDownEnter(ev, () => {
                  onRequestDisable()
                })
              }}
            />
          </InputGroup>
          <div>
            <Button
              color="danger"
              disabled={loadingVerfiy}
              onClick={() => {
                onRequestDisable()
              }}
            >
              {t("setting2fa:btn.Turn off 2FA", {
                name: getCurrentFA2MethodName(),
              })}
            </Button>
          </div>
        </div>
      )}
    </div>
  )
}

export default Generation2FA
